li, .logo, .nav-list {
  display: inline-block;
}

.nav-link {
  transition: all .3s;
}

.nav-link:hover {
  text-shadow: 0 5px 20px #8383835b;
  transform: translateY(-3px);
}

.nav-link:active {
  transform: translateY(-1px);
}

button {
  color: #fff;
  background-color: #cf6663;
  border: 1px solid #0000;
  border-radius: 5px;
  padding: .5rem;
}

.bg-primary {
  background-color: var(--bg-primary);
}

.font-primary {
  color: var(--font-primary);
}

