  background-color: #ce3f3a;
}

.h-50 {
  background-color: gray;
}

.hero {
  width: 100%;
  height: 80vh;
}

.hero-wrapper {
  background: linear-gradient(0deg, var(--bg-primary-filter-dark), var(--bg-primary-filter)), url("hero_new.ef1e6b3e.webp");
  clip-path: ellipse(65% 90% at 50% 10%);
  background-position: 50%;
  background-size: cover;
  background-attachment: fixed;
}

@media (max-width: 1149px) {
  .hero-wrapper {
    clip-path: none;
  }
}

.cta-section {
  background-color: #fff;
  padding-top: 1rem;
  padding-bottom: 8rem;
}

.cta-box {
  box-shadow: 0 20px 50px var(--bg-primary-filter);
  background-color: #fff;
  border: 1px solid #0000;
  border-radius: 10px;
}

@media (min-width: 1150px) {
  .cta-box {
    transform: translateY(-100px);
  }
}

.cta-box p {
  font-size: 1.7rem;
  font-weight: 400;
}

.cta-box button {
  font-size: larger;
}

.list li {
  font-style: italic;
  line-height: 3rem;
  list-style-image: url("home-icon.ab36e015.webp");
  list-style-type: circle;
  display: list-item;
}

#welcome {
  background: linear-gradient(0deg, var(--bg-primary-filter-dark), var(--bg-primary-filter)), url("brook-g2fab6e2b5_1024-min.43b0c9bf.webp");
  background-size: cover;
  background-attachment: fixed;
}

#why-choose-us {
