  background-size: cover;
  background-attachment: fixed;
}

.section-bg-img {
  background-position: 0%;
  background-repeat: no-repeat;
  background-size: cover;
}

.icon {
  filter: invert(75%) sepia(13%) saturate(1414%) hue-rotate(87deg) brightness(94%) contrast(90%);
