}

.rightleft {
  height: 4px;
  width: 40px;
  background-color: var(--darkpurpleprimary);
  border-radius: 2px;
  margin-top: 15px;
  transition: all .3s ease-in;
  position: absolute;
  transform: rotate(-45deg);
}

