  color: #fff;
  text-transform: uppercase;
  letter-spacing: 2px;
  opacity: 0;
  padding: 40px 0 0;
  font-family: Helvetica, Arial, sans-serif;
  font-size: .6em;
  transition: all .3s ease-in;
}

